<template>
  <div>
    <app-wallpaper-movie />

    <div class="container">
      <div>
        <app-searchbar />
        <!-- <div id="liveAlertPlaceholder"></div>
      <button
        type="button"
        class="btn btn-primary"
        id="liveAlertBtn"
        @click="alert"
      >
        Show live alert
      </button> -->
      </div>

      <h2 v-if="info == null">Cargando...</h2>

      <div class="d-flex flex-wrap justify-content-center">
        <div
          class="card mt-2"
          v-for="i in info"
          :key="i.id"
          style="width: 18rem"
        >
          <img :src="i.posterURL" alt="" class="card-img-top" />
          <div class="card-body">
            <h5 class="card-title">{{ i.title }}</h5>
          </div>
        </div>
      </div>

      <!-- <ul class="list-group">
      <li v-for="i in info" :key="i.id" class="list-group-item">
        <h2>{{ i.title }}</h2>
        <img :src="i.posterURL" alt="" />
      </li>
    </ul> -->
    </div>

    <!-- Carousel -->

    <div class="container mt-5">
      <hr />
      <h2>Recomendadas</h2>
      <b-carousel
        id="carousel-1"
        v-model="slide"
        :interval="2000"
        @sliding-start="onSlideStart"
        @sliding-end="onSlideEnd"
        v-if="info != null"
      >
        <!-- Slides with custom text -->
        <b-carousel-slide>
          <template #img >
            <img
              class="d-block img-fluid w-50 mx-auto"
              :src="info[13].posterURL"
              alt="image slot"
            />
          </template>
        </b-carousel-slide>

        <b-carousel-slide>
          <template #img>
            <img
              class="d-block img-fluid w-50 mx-auto"
              :src="info[15].posterURL"
              alt="image slot"
            />
          </template>
        </b-carousel-slide>

        <b-carousel-slide>
          <template #img>
            <img
              class="d-block img-fluid w-50 mx-auto"

              :src="info[0].posterURL"
              alt="image slot"
            />
          </template>
        </b-carousel-slide>
        <!-- Slides with image only -->
      </b-carousel>
    </div>
  </div>
</template>

<script>
// import axios from 'axios';
export default {
  name: "Main",
  data() {
    return {
      info: null,
      slide: 0,
      sliding: null,
    };
  },
  methods: {
    fetchData() {
      fetch("https://api.sampleapis.com/movies/western")
        .then((response) => {
          response.json().then((data) => {
            console.log(data);
            this.info = data;
          });
        })
        .catch((err) => {
          this.alert(`Error al cargar la API (vuelva a intentar): ${err}`);
          this.info = `Error*: ${err}`;
        });
    },
    alert() {
      const alertPlaceholder = document.getElementById("liveAlertPlaceholder");
      const appendAlert = (message, type) => {
        const wrapper = document.createElement("div");
        wrapper.innerHTML = [
          `<div class="alert alert-${type} alert-dismissible" role="alert">`,
          `   <div>${message}</div>`,
          '   <button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>',
          "</div>",
        ].join("");

        alertPlaceholder.append(wrapper);
      };

      const alertTrigger = document.getElementById("liveAlertBtn");
      if (alertTrigger) {
        alertTrigger.addEventListener("click", () => {
          appendAlert("Nice, you triggered this alert message!", "success");
        });
      }
    },
    onSlideStart(slide) {
      this.sliding = true;
    },
    onSlideEnd(slide) {
      this.sliding = false;
    },
  },
  mounted() {
    this.fetchData();
    // axios
    //   .get('https://api.coindesk.com/v1/bpi/currentprice.json')
    //   .then(response => (this.info = response))
    //   .catch(err => (this.info = "Error papu"))
  },
};
</script>

<style>
.class-name {
  height: 400px;
}
</style>



